import axios from 'axios'
import {useState, useEffect} from 'react'

const Login = () => {

    const [customers, setCustomers] = useState([])

    useEffect(() => {
         axios({
             method: 'GET',
             url: 'http://localhost:8000/api/customers/'
         })
         .then(response => {
             setCustomers(response.data)
             console.log(response.data)
         })
    }, [])
    console.log(customers)
    const [login, setLogin] = useState('')

    const handleLogin = (e) => {
      let log = e.target.value
      setLogin(log)
      console.log(log)
    }

    const [password, setPass] = useState('')

    const handlePass = (e) => {
      let pass = e.target.value
      setPass(pass)
      console.log(pass)
    }
  
     let text = 'Match'

     const matchVal = () => {
         if(login == customers[0].id) {
             text = "Есть совпадение"
         } else {
             text = "Не совпало"
         }
     }

    return <>
        {customers.map(c => {
            if(c.id == !login) text = "Есть совпадение"
            else text = "Не совпало"
        })}
        {text}
      <form>
      <div class="form-group">
    <label for="exampleInputEmail1">Login</label>
    <input type="email" value={login} onChange={(e)=> handleLogin(e)} class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Login"/>
    <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
  </div>
  <div class="form-group">
    <label for="exampleInputPassword1">Password</label>
    <input type="password" value={password} onChange={(e)=> handlePass(e)} class="form-control" id="exampleInputPassword1" placeholder="Password"/>
  </div>
  <div class="form-check">
    <input type="checkbox" class="form-check-input" id="exampleCheck1"/>
    <label class="form-check-label" for="exampleCheck1">Check me out</label>
  </div>
  <button type="button" onClick={()=> matchVal()} class="btn btn-primary">Submit</button>
      </form>
    </>
}
export default Login